export default {
  data() {
    return {
      fams: [],
      famsteste: [],
      demand: [],
      farmDemandIndex: Number
    };
  },
  methods: {
    fetchImageURL() {
      this.axios.get(`https://agroxdev.rj.r.appspot.com/web/demand`).then(response => {
        this.fams = response.data;
        this.fams.splice(0, 1);
        this.fams.splice(0, 1);
        this.fams.splice(0, 1);
        this.fams.splice(9, 1);

        console.log('teste', this.fams);
        // this.img = btoa(
        //   new Uint8Array(response.data).reduce(
        //     (data, byte) => data + String.fromCharCode(byte),
        //     ''
        //   )
        // );

        // resolve(response.request._response);
      });
    },

    filterFarm(data) {
      this.demand = [];
      this.farmDemandIndex = data;
      this.famsteste = this.fams[data].many_user_has_many_farm[0].farm.area;
      this.famsteste.forEach(element => {
        if (element.demand != null) {
          this.demand.push(JSON.parse(element.demand));
          console.log(this.demand);
        }
      });
      // console.log(thi)
    }
  },
  mounted() {
    this.fetchImageURL();
  }
};
